import Link from 'next/link'
import Modal from 'react-awesome-modal';


const Header_Style = {
    display : 'flex',
    justifyContent: 'left',
    alignItems: 'center',
    backgroundColor : '#999',
    height : '80px',
    minWidth : '600px',
    marginBottom : '20px',
}


function Header(){
  return(
    <header className="header" style={Header_Style}>

    <style jsx>{`
      .logo {
        font-size : 30px;
        margin-left : 15px;
        text-decoration : none;
        color : black;
      }

      .logo:hover {
        opacity : 0.5;
      }

      .achievement {
        font-size : 15px;
        margin-left : 40%;
        text-decoration : none;
        color : black;
      }

      .achievement:hover {
        opacity : 0.5;
      }
    `}</style>

      <Link href="/">
        <a className="logo">Todo Application</a>
      </Link>
      <Link href="/">
        <a className=" achievement" >완료목록 보기</a>
      </Link>
      <Modal
          visible={this.state.visible}
          width="500"
          height="400"
          effect="fadeInUp"
          onClickAway={() => this.closeModal()}
      >
          <div>
              <h1 className="title">계획 추가하기</h1>
              <Form_Left />
              <a className="close" href="javascript:void(0);" onClick={() => this.closeModal()}>닫기</a>
          </div>
      </Modal>
    </header>
  )
}

export default Header
